.Reset {
    background: none;
    border: 2px solid #000;
    border-radius: 5px;
    padding: 10px 30px;
    font-size: 20px;
    margin: 20px 0;
    cursor: pointer;
    -webkit-transition: 0.2s;
    -o-transition: 0.2s;
    transition: 0.2s;
}
.Reset:hover {
    background: #000;
    color: #fff;
}

.Reset:focus {
    outline: none;
}


